.global--logo {
    color: #333;
    text-align: center;
    text-transform: uppercase;

    &:hover {
        text-decoration: none;
    }

    h1 {
        line-height: 0.2;
        font-weight: 600;
    }

    span {
        font-size: 10px;
    }
}

.global--main-nav {
    width: 100%;
    height: 90px;
    // position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    transition: 0.35s ease-in;
    background-color: #f7f7f7;

    &-left {
        img {
            margin-top: 5px;
            width: 250px;
        }
    }

    &-right {
        display: none;
        @include mq (medium) {
            display: block;
        }
    }

    &-item {
        padding-left: 0.75em;
        padding-right: 0.75em;
        color: #666666;
        text-transform: uppercase;
        font-size: 0.95rem;

        &:hover {
            color: #adadad;
            text-decoration: none;
        }
    }
}

.headroom--not-top {
    height: 75px;
    transition: 0.35s ease-in;

    img {
        margin-top: 7px;
        width: 100px;
    }

    .global--main-nav-item {
        font-size: 0.75rem;
    }
}
